<template>
  <div class="container">
    <div>
      <v-row class="chat-history">
        <v-col :key="card" cols="12" v-for="card in cards">
          <v-card elevation="0" outlined class="py-2">
            <v-card-text class="text-body-1">{{ card }}</v-card-text>
            <v-card
              elevation="0"
              v-if="card.command && card.command.name === 'about'"
              class="my-2"
            >
              <about-vue></about-vue>
            </v-card>
            <v-card
              elevation="0"
              v-else-if="card.command && card.command.name === 'basicInfo'"
              class="my-2"
            >
              <basic-info-vue></basic-info-vue>
            </v-card>
            <v-card
              elevation="0"
              v-else-if="card.command && card.command.name === 'compInfo'"
              class="my-2"
            >
              <comp-info-vue></comp-info-vue>
            </v-card>
            <v-card
              elevation="0"
              v-else-if="card.command && card.command.name === 'histExp'"
              class="my-2"
            >
              <hist-exp-vue></hist-exp-vue>
            </v-card>
            <v-card
              elevation="0"
              v-else-if="card.command && card.command.name === 'simiExp'"
              class="my-2"
            >
              <simi-exp-vue></simi-exp-vue>
            </v-card>
            <v-card
              elevation="0"
              v-else-if="card.command && card.command.name === 'home'"
              class="my-2"
            >
              <home-vue></home-vue>
            </v-card>
          </v-card>
        </v-col>
      </v-row>
    </div>
    <div class="chat-input">
      <v-textarea
        class="mx-5 mt-3"
        @click:append="sendClick"
        @keydown.enter="sendClick"
        append-icon="mdi-send"
        auto-grow
        clearable
        rounded
        rows="1"
        solo
        v-model="msg"
      ></v-textarea>
    </div>
  </div>
</template>
<script>
import { chat, stock_id } from "@/api/chat_api";
import { notify } from "@/components/notification";
import HomeVue from "./Home.vue";
import AboutVue from "./About.vue";
import BasicInfoVue from "./BasicInfo.vue";
import CompInfoVue from "./CompInfo.vue";
import HistExpVue from "./HistExp.vue";
import SimiExpVue from "./SimiExp.vue";
import store from "../store";
import { getFilters , getStocksByFilter} from "@/api/home_api";
import {cleanMeta, initHeaders2, reMapData} from "@/utils";
export default {
  components: {
    HomeVue,
    BasicInfoVue,
    CompInfoVue,
    AboutVue,
    HistExpVue,
    SimiExpVue,
  },
  data() {
    return {
      cards: [
        "‰Ω†Â•Ω",
        "‰Ω†Â•ΩüëãÔºåÊàëÊòØÂü∫‰∫é ChatGPT ÂºÄÂèëÁöÑËÇ°Á•®Âä©ÊâãÔºåÂèØ‰ª•Ê†πÊçÆÊÇ®ÁöÑÈóÆÈ¢òËá™Âä®Â±ïÁ§∫ËÇ°Á•®‰ø°ÊÅØÔºå‰æãÂ¶ÇÊÇ®ÂèØ‰ª•ÊèêÈóÆÊàëÔºöÊòéÂæ∑ÁîüÁâ©ÊòéÂ§©ÁöÑËµ∞ÂäøÂ¶Ç‰ΩïÔºü",
      ],
      msg: "",
    };
  },
  methods: {
    sendClick() {
      this.cards.push(this.msg);
      notify("success", "Ê≠£Âú®Ëé∑ÂèñÂõûÂ∫î");
      chat(this.msg).then((res) => {
        if (res.status !== 200) {
          notify("error", "Ëé∑Âèñ API Ë∞ÉÁî®Â§±Ë¥•");
          return;
        }

        let pageName = res.data.command.name;
        if (pageName !== "do_nothing") {
          if (Object.keys(res.data.command.args).length !== 0) {
            let stock_name = res.data.command.args.stock_name;
            if (stock_name === "<stock_name>") {
              this.cards.push(res.data);
              return;
            }
            notify("success", "Ê≠£Âú®Ëé∑ÂèñËÇ°Á•®‰ª£Á†Å");
            stock_id(stock_name).then((stock_res) => {
              if (res.status !== 200) {
                notify("error", "Ëé∑Âèñ API Ë∞ÉÁî®Â§±Ë¥•");
                return;
              }
              store.commit("setItem", {
                stock_id: stock_res.data.stock_id,
                _stock_name: stock_name,
                // !!! ‰∏∫‰∫Ü CompInfo
                headers1: stock_name,
              });
              this.cards.push(res.data);
            });
          } else {
            this.cards.push(res.data);
          }
          console.log(res.data);
        } else {
          this.cards.push(res.data);
          this.cards.push("ËØ∑Êèê‰æõÊõ¥Â§ö‰ø°ÊÅØ");
        }
        this.msg = "";
      });
    },
    initFilters() {
      getFilters()
        .then((res) => {
          let filters = res.data.data;
          let list = [];
          for (let filtersKey in filters) {
            list.push(filters[filtersKey]);
          }
          this.filters = list;
          this.selected = list[list.length - 1];
          this.initStock();
          notify("success", "Êï∞ÊçÆÊõ¥Êñ∞ÊàêÂäü");
        })
        .catch();
    },
    initData() {
      this.initFilters();
    },
    initStock() {
      getStocksByFilter(this.selected)
        .then((res) => {
          if(res.data.status===200){
            let headers = res.data.headers;
            let data = res.data.data;
            store.commit("setMetaData", JSON.parse(JSON.stringify(res.data.data)));
            headers = cleanMeta(headers);
            initHeaders2(headers,this);
            reMapData(data,headers);
            this.stocks = data;
            notify("success", "Êï∞ÊçÆÊõ¥Êñ∞ÊàêÂäü");
          }else {
            notify("error", "ËÇ°Á•®Êï∞ÊçÆÊõ¥Êñ∞Â§±Ë¥•");
          }
        })
        .catch();
    },
  },
  created() {
    this.initData();
  },
};
</script>
<style>
.container {
  display: grid;
  grid-template-rows: 1fr auto;
  height: 90vh;
}

.chat-input {
  grid-row: 2;
}

.chat-history {
  overflow-y: auto;
}
</style>