<script>
import {notify} from "@/components/notification";
import {getRelated, getUnRelated} from "@/api/similarity _api";
import store from "@/store";
import router from "@/router";
import {cleanMeta, initHeaders2, reMapData} from "@/utils";

export default {
  data() {
    return {
      tabs: null,
      id:"",
      tabsTitle: ['Âú∫ÊôØÁõ∏ÂÖ≥Áõ∏‰ºº‰∏™ËÇ°', 'Âú∫ÊôØÊó†ÂÖ≥Áõ∏‰ºº‰∏™ËÇ°'],
      dialog: false,//ÂºπÁ™óÊéßÂà∂
      headers:[],
      footers: {
        showCurrentPage: true,
        itemsPerPageText: 'ÊØèÈ°µËÆ∞ÂΩïÊï∞',
        showFirstLastPage: true
      },
      stocks:[],
      stocks_none:[]
    }
  },
  methods:{
    initRelatedStock(){
      let id = this.$store.state.item.stock_id
      let time = this.$store.state.selected;
      getRelated(id, time).then(
        res =>{
          if(res.data.status===200){
            let headers = res.data.headers;
            headers = cleanMeta(headers);
            let data = res.data.data;
            initHeaders2(headers,this);
            reMapData(data,headers);
            this.stocks=data;
            notify("success", "Êï∞ÊçÆÊõ¥Êñ∞ÊàêÂäü");
          }else {
            notify("error", "Áõ∏‰ººÂ∫¶Êï∞ÊçÆÊöÇ‰∏çÂ≠òÂú®");
          }
        }
      ).catch();
    },
    initUnRelatedStock(){
      let id = this.$store.state.item.stock_id
      let time = this.$store.state.selected;
      getUnRelated(id, time).then(
          res =>{
            if(res.data.status===200){
              let headers = res.data.headers;
              headers = cleanMeta(headers);
              let data = res.data.data;
              initHeaders2(headers,this);
              reMapData(data,headers);
              this.$set(this,"stocks_none",data);
              // this.stocks=data;
              notify("success", "Êï∞ÊçÆÊõ¥Êñ∞ÊàêÂäü");
            }else {
              notify("error", "Áõ∏‰ººÂ∫¶Êï∞ÊçÆÊöÇ‰∏çÂ≠òÂú®");
            }
          }
      ).catch();
    },
    changeItem(goto, item) {
      let stock = item.headers1;
      item.stock_id=item["ËÇ°Á•®‰ª£Á†Å"];
      store.commit("setTitle2", "‰∏™ËÇ°ÁîªÂÉè" + "‚Äî‚Äî" + stock);
      store.commit("setItem", item);
      store.commit("setSelected", this.selected);
      if (goto) {
        router.push(goto);
      }
    },
    changeTab(tab){
      switch (tab) {
        case 1:
          this.initRelatedStock();
          break;
        case 2:
          this.initUnRelatedStock();
          break;
      }
    },
  },
  created() {
    this.initRelatedStock();
  }
}
