import { axios } from "./request";
import { URL } from "./base_url";

export function chat(content) {
    return axios({
        url: `${URL}/chat`,
        method: "post",
        data: {
            content: content
        }
    });
}

export function stock_id(stock_name) {
    return axios({
        url: `${URL}/stock_id/${stock_name}`,
        method: "get",
    });
}